a {
  color: inherit;
  font-size: 1.8vw;
  font-weight: bold;
  text-decoration: none;
}

a:hover, a:active {
  color: #3354f8;
}

.back-link {
  color: inherit;
  align-items: center;
  padding-left: 4vw;
  font-weight: bold;
  text-decoration: none;
  display: inline-flex;
  position: relative;
}

.back-link:hover {
  color: #3354f8;
}

.back-link:before {
  content: "←";
  font-size: 3.8vw;
  transition: color .3s;
  animation: 3s infinite moveArrow;
  position: absolute;
  top: -3.2vh;
  left: 0;
}

@keyframes moveArrow {
  0% {
    left: -7vw;
  }

  50% {
    left: 0;
  }

  51% {
    left: 0;
  }

  100% {
    left: -7vw;
  }
}

.colorPositionButtons {
  justify-content: center;
  align-items: center;
  gap: 10vw;
  height: 100vh;
  display: flex;
}

button {
  color: #fff;
  background-color: #3354f8;
  border: 3px solid #f0f8ff;
  border-radius: 25% 15%;
  justify-content: center;
  align-items: center;
  width: 20vw;
  height: 10vh;
  font-size: 1.8vw;
  font-weight: bold;
  transition: background-color 1.5s, transform 2s;
  display: flex;
}

.inactive {
  color: #fff;
  pointer-events: none;
  background-color: #3354f84d;
  font-size: 1.8vw;
  font-weight: bold;
  transform: rotateX(1080deg);
}

.active {
  transition: transform 1s;
  transform: rotateX(0);
}
